import React from 'react';
import { Card } from 'antd';
import Banner from './Banner';
import co5 from '../img/co5.jpg';
import co6 from '../img/co6.jpg';
import co7 from '../img/co7.jpg';

const { Meta } = Card;

function Contain() {

  return (
    <div>
      <Banner />
      <div style={{
        width: '900px',
        margin: '30px auto',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}>

        <Card
          style={{
            width: 280,
            height: 300,
            margin: '20px',
          }}
          cover={
            <img
              alt="example"
              src={co5}
            />
          }
        >
          <Meta
            title=" "
            description="      ."
          />
        </Card>

        <Card
          style={{
            width: 280,
            height: 300,
            margin: '20px',
          }}
          cover={
            <img
              alt="example"
              src={co6}
            />
          }
        >
          <Meta
            title=" "
            description="             ."
          />
        </Card>

        <Card
          style={{
            width: 280,
            height: 300,
            margin: '20px',
          }}
          cover={
            <img
              alt="example"
              src={co7}
            />
          }
        >
          <Meta
            title=" "
            description="   . ,            "
          />
        </Card>

      </div>
    </div>
  )
}

export default Contain
